html,
body {
  height: 100%;
  padding: 0;
  margin: 0;
  position: relative;
}

html {
  font-size: 0.875rem;
}

body {
  margin: 0;
  font-size: 0.875rem;
  background-color: #e1f0f8;
  font-family: Arial, sans-serif;
  line-height: 1.42;
}

.wrapper {
  min-height: 100%;
  height: auto;
  overflow: hidden;
  position: relative;
}

.container {
  width: 1145px;
  height: 100%;
  margin: 0 auto;
  position: relative;
}

.content {
  padding-top: 35px;
  padding-bottom: 35px;
}

.content__section {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding-top: 30px;
}

.content__block {
  display: inline-block;
  width: 77%;
}

ul {
  margin: 0;
  padding: 0;
}

ul li {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: normal;
}

* {
  box-sizing: border-box;
}

.header {
  display: flex;
  width: 100%;
  height: 158px;
  background-color: #fff;
  align-items: flex-end;
  padding: 15px 25px 20px;
  justify-content: space-between;
}

.logo {
  display: block;
  width: 244px;
  height: 122px;
  overflow: hidden;
}

.logo__img {
  max-width: 100%;
  max-height: 100%;
}

.header__calculate {
  display: inline-block;
  position: relative;
  margin-left: auto;
  padding-bottom: 17px;
  font-size: 23px;
  color: #f36240;
  line-height: 1;
  text-decoration: none;
}

.header__calculate:after {
  display: block;
  content: '';
  position: absolute;
  width: 100%;
  background-color: #f36240;
  height: 3px;
  position: absolute;
}

.header__contact {
  padding-left: 94px;
  text-align: right;
  padding-bottom: 6px;
}

.header__time {
  font-size: 16px;
  color: #5f6973;
}

.header__phone {
  display: inline-block;
  text-decoration: none;
  font-size: 31px;
  line-height: 1.3;
  color: #5f6973;
}

.nav {
  width: 100%;
  background-color: #fff;
}

.nav__list {
  display: flex;
  width: 100%;
}

.nav__item {
  display: block;
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: 18%;
  border-right: 1px solid #5f6973;
}

.nav__item:last-child {
  border-right: 0;
}

.nav__link {
  display: block;
  padding-top: 10px;
  padding-bottom: 14px;
  padding-left: 15px;
  padding-right: 15px;
  text-align: center;
  font-size: 24px;
  color: #5f6973;
  white-space: nowrap;
  text-decoration: none;
}

.nav__link:hover {
  background-color: #7bc3d5;
  color: #fff;
}

.title__main {
  font-size: 64px;
  line-height: 1.2;
  color: #4b4b4b;
}

.title__description {
  font-size: 26px;
  color: #4b4b4b;
}

.recommendation {
  display: inline-block;
  width: 21%;
  padding: 17px 15px 19px 23px;
  border-radius: 5px;
  background-color: #fff;
}

.recommendation__title {
  display: block;
  font-size: 20px;
  color: #4b4b4b;
  padding-bottom: 20px;
}

.recommendation__item {
  padding-bottom: 12px;
}

.recommendation__link {
  font-size: 16px;
  color: #8a8a8a;
}

.recommendation__link:hover {
  color: #f36240;
}

.main {
  border-radius: 5px;
  background-color: #fff;
  padding: 20px 25px 30px;
  height: 610px;
  margin-bottom: 25px;
}

.main__title {
  font-size: 26px;
  color: #484848;
  padding-bottom: 29px;
}

.main__block {
  display: flex;
}

.main__pig {
  width: 366px;
  padding-right: 32px;
  text-align: center;
}

.main__img {
  max-width: 100%;
}

.main__img-caption {
  padding-top: 40px;
  font-size: 18px;
  color: #a6a6a6;
}

.main__list {
  padding-bottom: 35px;
}

.main__item {
  padding-left: 19px;
  margin-bottom: 8px;
  font-size: 14px;
  color: #484848;
  position: relative;
  white-space: nowrap;
}

.main__item:before {
  display: inline-block;
  position: absolute;
  top: 50%;
  transform: translateY(-3px);
  left: 3px;
  content: '';
  border-radius: 50%;
  width: 6px;
  height: 6px;
  background-color: #f36240;
}

.main__item:last-child {
  margin-bottom: 0;
}

.form__title {
  font-size: 20px;
  color: #484848;
  padding-bottom: 17px;
}

.form__list {
  display: flex;
  width: 100%;
  padding-bottom: 27px;
  justify-content: space-between;
}

.form__item {
  margin-right: 8px;
}

.form__item:last-child {
  margin-right: 0;
}

.form__radio {
  display: none;
}

.form__radio-custom {
  position: relative;
  border-radius: 3px;
  width: 61px;
  height: 59px;
}

.form__radio-custom--1 {
  background: url("../img/bg1.jpg") center center no-repeat;
  background-size: cover;
}

.form__radio-custom--2 {
  background: url("../img/bg2.jpg") center center no-repeat;
  background-size: cover;
}

.form__radio-custom--3 {
  background: url("../img/bg3.jpg") center center no-repeat;
  background-size: cover;
}

.form__radio-custom--4 {
  background: url("../img/bg4.jpg") center center no-repeat;
  background-size: cover;
}

.form__radio-custom--5 {
  background: url("../img/bg5.jpg") center center no-repeat;
  background-size: cover;
}

.form__radio-custom--6 {
  background: url("../img/bg6.jpg") center center no-repeat;
  background-size: cover;
}

.form__radio-custom--7 {
  background: url("../img/bg7.jpg") center center no-repeat;
  background-size: cover;
}

.form__radio:checked + .form__radio-custom::after {
  content: "";
  display: block;
  background-color: #fff;
  position: absolute;
  left: 13px;
  top: 30px;
  width: 18px;
  height: 5px;
  transform: rotate(45deg);
}

.form__radio:checked + .form__radio-custom::before {
  content: "";
  display: block;
  position: absolute;
  left: 22px;
  top: 27px;
  width: 30px;
  height: 5px;
  transform: rotate(-45deg);
  background-color: #fff;
}

.form__more {
  font-size: 14px;
  color: #484848;
  padding-bottom: 41px;
}

.form__submit {
  display: block;
  border-radius: 5px;
  background-color: #eb5825;
  padding: 11px 40px;
  border: 0;
  font-size: 18px;
  color: #fff;
}

.form__submit:focus {
  outline: none !important;
}

.form__order {
  display: block;
  padding-top: 10px;
  font-size: 14px;
  color: #8a8a8a;
}

.description {
  border-radius: 5px;
  background-color: #fff;
  height: 480px;
  padding-left: 29px;
  padding-right: 30px;
  padding-bottom: 36px;
  color: #484848;
  margin-bottom: 30px;
}

.description__title {
  font-size: 26px;
  padding-top: 27px;
  padding-bottom: 15px;
}

.description__title--second {
  padding-top: 24px;
}

.description__item {
  display: flex;
  margin-bottom: 19px;
}

.description__item:last-child {
  margin-bottom: 0;
}

.description__item--margin {
  align-items: center;
  margin-bottom: 8px;
}

.description__img {
  display: block;
  width: 60px;
  height: 60px;
  margin-right: 30px;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

.description__img--bg1 {
  background-image: url("../img/desc1.png");
}

.description__img--bg2 {
  background-image: url("../img/desc2.png");
}

.description__text {
  max-width: 746px;
}

.description__text--small {
  max-width: 700px;
}

.description__number {
  border: 2px solid #484848;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  text-align: center;
  font-size: 20px;
  margin-right: 15px;
}

.review__header {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  padding-bottom: 15px;
}

.review__title {
  font-size: 26px;
  color: #484848;
}

.review__more-review {
  font-size: 16px;
  color: #eb5825;
}

.review__article {
  border-radius: 5px;
  background-color: #fff;
  padding: 32px 27px;
  height: 319px;
}

.review__name {
  font-size: 16px;
  color: #484848;
}

.review__order,
.review__time {
  font-size: 14px;
  color: #8a8a8a;
}

.review__time {
  padding-bottom: 6px;
}

.review__stars {
  margin-bottom: 5px;
}

.review__stars-img {
  width: 15px;
  height: 15px;
  margin-right: 1px;
}

.review__text {
  font-size: 14px;
  line-height: 1.2;
  color: #484848;
  height: 56px;
  overflow-y: hidden;
  margin-bottom: 8px;
}

.review__link {
  display: block;
  font-size: 16px;
  color: #eb5825;
  margin-bottom: 41px;
}

.review__add-review {
  border-radius: 5px;
  background-color: #eb5825;
  font-size: 18px;
  color: #fff;
  padding: 14px 25px;
  text-decoration: none;
}